@charset "UTF-8";
/**
 * @fileOverview reset基础样式
 */
* {
  webkit-tap-highlight-color: transparent;
  touch-action: pan-y; }

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li,
fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure, footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  /*vertical-align: baseline; delete by jican*/ }

html, body {
  width: 100%;
  height: 100%; }

a {
  color: inherit;
  text-decoration: none; }

a:focus, a:hover, a:visited {
  outline: 0;
  text-decoration: none; }

a:active, a:hover {
  outline: 0; }

div {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

input {
  outline: none;
  font-size: 14px; }

html, body, form, fieldset, p, div, h1, h2, h3, h4, h5, h6 {
  -webkit-text-size-adjust: none; }

h1, h2, h3, h4, h5, h6 {
  font-size: 14px;
  font-weight: normal; }

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

a {
  text-decoration: none; }

body {
  -webkit-text-size-adjust: none;
  font-size: 14px;
  color: #333;
  background: #f5f5f5;
  font-family: Arial,Helvetica,STHeiTi,sans-serif; }

ol, ul, li {
  list-style: none; }

blockquote, q {
  quotes: none; }

img, table {
  border: 0; }

blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none; }

ins {
  text-decoration: none; }

del {
  text-decoration: line-through; }

select {
  -webkit-appearance: button;
  border: 0px; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.clearfix:after {
  content: '\0020';
  display: block;
  height: 0;
  clear: both; }

.clearfix {
  *zoom: 1; }

#root {
  width: 100%;
  height: 100%; }

.singleMusic {
  height: 1.33rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border-bottom: 1px solid #666;
  margin-left: .13rem;
  margin-right: .13rem; }
  .singleMusic img {
    width: 1.07rem;
    height: 1.07rem;
    margin-left: .13rem;
    margin-right: .27rem;
    border-radius: .13rem; }
  .singleMusic .singleMusic_trumpet {
    display: none;
    margin-left: .13rem;
    margin-right: .13rem; }
  .singleMusic .singleMusic_info {
    position: relative;
    width: 100%;
    height: 100%;
    overflow: hidden; }
    .singleMusic .singleMusic_info .music_name {
      position: relative;
      justify-content: flex-start;
      padding-top: .07rem;
      width: 90%;
      height: 60%;
      color: #333;
      font-size: 16px;
      text-align: justify;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis; }
      [data-dpr="2"] .singleMusic .singleMusic_info .music_name {
        font-size: 32px; }
      [data-dpr="3"] .singleMusic .singleMusic_info .music_name {
        font-size: 48px; }
      .singleMusic .singleMusic_info .music_name .music_alias {
        color: #666; }
    .singleMusic .singleMusic_info .music_author {
      position: relative;
      margin-top: -.07rem;
      width: 100%;
      height: 40%;
      color: #999999;
      display: flex;
      justify-content: flex-start; }
      .singleMusic .singleMusic_info .music_author .line {
        margin-left: .07rem;
        margin-right: .07rem; }
  .singleMusic .singleMusic_video {
    margin-right: .33rem; }
  .singleMusic .singleMusic_more {
    margin-right: .2rem; }
