@charset "UTF-8";
/**
 * @fileOverview reset基础样式
 */
* {
  webkit-tap-highlight-color: transparent;
  touch-action: pan-y; }

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li,
fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure, footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  /*vertical-align: baseline; delete by jican*/ }

html, body {
  width: 100%;
  height: 100%; }

a {
  color: inherit;
  text-decoration: none; }

a:focus, a:hover, a:visited {
  outline: 0;
  text-decoration: none; }

a:active, a:hover {
  outline: 0; }

div {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

input {
  outline: none;
  font-size: 14px; }

html, body, form, fieldset, p, div, h1, h2, h3, h4, h5, h6 {
  -webkit-text-size-adjust: none; }

h1, h2, h3, h4, h5, h6 {
  font-size: 14px;
  font-weight: normal; }

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block; }

a {
  text-decoration: none; }

body {
  -webkit-text-size-adjust: none;
  font-size: 14px;
  color: #333;
  background: #f5f5f5;
  font-family: Arial,Helvetica,STHeiTi,sans-serif; }

ol, ul, li {
  list-style: none; }

blockquote, q {
  quotes: none; }

img, table {
  border: 0; }

blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none; }

ins {
  text-decoration: none; }

del {
  text-decoration: line-through; }

select {
  -webkit-appearance: button;
  border: 0px; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.clearfix:after {
  content: '\0020';
  display: block;
  height: 0;
  clear: both; }

.clearfix {
  *zoom: 1; }

#root {
  width: 100%;
  height: 100%; }

.find {
  height: 100%; }
  .find .findTabs {
    height: 1.07rem;
    line-height: 1.07rem; }
    .find .findTabs .ant-tabs-nav {
      width: 60%;
      left: 50%;
      transform: translateX(-50%); }
      .find .findTabs .ant-tabs-nav .ant-tabs-tab {
        height: 1.07rem;
        width: 50%;
        font-size: 18px;
        text-align: center;
        margin: 0; }
        [data-dpr="2"] .find .findTabs .ant-tabs-nav .ant-tabs-tab {
          font-size: 36px; }
        [data-dpr="3"] .find .findTabs .ant-tabs-nav .ant-tabs-tab {
          font-size: 54px; }
      .find .findTabs .ant-tabs-nav .ant-tabs-ink-bar {
        width: 1rem !important;
        left: 17%; }
    .find .findTabs .mainList {
      height: 2.13rem;
      border-bottom: 1px solid #555; }
      .find .findTabs .mainList li {
        position: relative;
        float: left;
        width: 25%;
        height: 100%; }
        .find .findTabs .mainList li .icoWrapper {
          margin: auto;
          width: 70%;
          height: 1.6rem;
          display: flex;
          justify-content: center;
          align-items: center; }
          .find .findTabs .mainList li .icoWrapper .ico {
            display: block;
            width: 1.07rem;
            height: 1.07rem;
            background: #E21A1A;
            border-radius: 50%;
            color: #fff;
            text-align: center;
            line-height: 1.07rem; }
        .find .findTabs .mainList li .tag_name {
          width: 100%;
          height: .53rem;
          line-height: .4rem;
          text-align: center;
          color: #333;
          font-size: 14px; }
          [data-dpr="2"] .find .findTabs .mainList li .tag_name {
            font-size: 28px; }
          [data-dpr="3"] .find .findTabs .mainList li .tag_name {
            font-size: 42px; }
